<template>
  <v-card
    class="mx-auto my-2"
    outlined
  >
    <v-list-item class="px-2">
      <v-list-item-avatar
        tile
        size="150"
        v-if="imageUrl"
      >
        <v-img
          :src="imageUrl"
          height="150"
          width="150"
        ></v-img>
      </v-list-item-avatar>
      <div class="pl-2" v-else></div>

      <v-list-item-content
        class="px-0 d-flex align-self-start"
        style="min-height: 150px"
      >
        <v-list-item-title class="title mb-1">{{ title }}</v-list-item-title>
        <div>{{ description }}</div>

        <div class="d-flex align-self-end pr-2">
          <div class="d-flex align-self-center">
            {{ price }} $  ×  {{ selected }}
          </div>

          <v-spacer />

          <div>
            <v-btn
              color="primary"
              @click="remove()"
              depressed
            >
              remove
            </v-btn>
          </div>
        </div>
      </v-list-item-content>
    </v-list-item>
  </v-card>
</template>

<script>
export default {
  name: 'CartItem',

  props: {
    id: {
      type: Number,
      required: true
    },
    title: {
      type: String,
      required: true
    },
    description: {
      type: String,
    },
    imageUrl: {
      type: String,
    },
    price: {
      type: String,
    },
  },

  data: () => ({
    selected: 0,
  }),

  created() {
    this.selected = this.$store.getters.getItem(this.id)
  },

  methods: {
    remove() {
      this.$store.commit('setItem', {id: this.id, val: 0})
    }
  },
}
</script>

